import React, { createContext, useState, useEffect } from 'react'
import isTouchDevice from 'is-touch-device'

const MAX_VALUE = 15
const ADDITION_VS_SUBTRACTION_RATIO = 0.4

const AppContext = createContext()

const AppProvider = props => {
  const [touchDevice, setTouchDevice] = useState(null)
  const [mathProblem, setMathProblem] = useState(null)
  const [answer, setAnswer] = useState(null)

  const [leftState, setLeftState] = useState(0)
  const [leftTimer, setLeftTimer] = useState(0)
  const [leftCorrect, setLeftCorrect] = useState(null)

  const [rightState, setRightState] = useState(0)
  const [rightTimer, setRightTimer] = useState(0)
  const [rightCorrect, setRightCorrect] = useState(null)

  const [showHelp, setShowHelp] = useState(0)

  const randomNumber = () => {
    return Math.floor(Math.random() * MAX_VALUE + 1)
  }

  const generateMathProblem = () => {
    const operations = ['+', '-']
    const operation = Math.random() <= ADDITION_VS_SUBTRACTION_RATIO ? operations[0] : operations[1]
    let num1 = randomNumber()
    let num2 = randomNumber()
    while (num2 === num1) {
      num2 = randomNumber()
    }
    const bigger = Math.max(num1, num2)
    num1 = bigger === num1 ? num2 : num1

    if (operation === '+') {
      num2 = bigger - num1
      setMathProblem(`${num1} + ${num2}`)
      setAnswer(bigger)
    } else {
      setMathProblem(`${bigger} - ${num1}`)
      setAnswer(bigger - num1)
    }
  }

  // do on initial load only
  useEffect(() => {
    generateMathProblem()
    setTouchDevice(isTouchDevice())
  }, [])

  // compute correct answers
  useEffect(() => {
    if (rightState !== 2) setRightCorrect(null)
    else setRightCorrect(rightTimer === answer)
  }, [rightState, rightTimer])

  useEffect(() => {
    if (leftState !== 2) setLeftCorrect(null)
    else setLeftCorrect(leftTimer === answer)
  }, [leftState, leftTimer])

  return (
    <AppContext.Provider
      value={{
        touchDevice,
        answer,
        mathProblem,
        generateMathProblem,
        showHelp,
        setShowHelp,

        leftState,
        setLeftState,
        rightState,
        setRightState,

        leftTimer,
        setLeftTimer,
        rightTimer,
        setRightTimer,

        leftCorrect,
        rightCorrect
      }}
    >
      {props.children}
    </AppContext.Provider>
  )
}

export { AppContext, AppProvider }
